/* Header.css */
.header {
    background-color: #141414;
    height: 10vh;
    width: 100%;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1000; /* Ensure it stays above other content */
  }
  
  .left-wrapper {
    display: flex;
    align-items: center;
    margin-right: auto;
    flex-direction: row;
  }
  
  .right-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 10px;
    flex-direction: row;
  }
  
  .text-css {
    height: 50%;
    width: 100%;
    color: #ffffff;
    margin-right: 10px;
    cursor: pointer;
  }
  
  .button {
    height: 50%;
    width: 100%;
    color: #000000;
    margin-right: 10px;
    margin-left: 10px;
    white-space: nowrap; /* Prevents the text inside the button from wrapping */
  }
  
  .search-container {
    display: flex;
    align-items: center;
  }
  